}

.bg-overlay {
  position: relative;
  overflow: hidden;
}
.bg-overlay::before {
  content: "";
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: #0000007a;
  position: absolute;
}
.bg-overlay > * {
  position: relative;
}

.loader1mk {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.loaderbg {
  position: fixed;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
}

.loader-logo {
  position: relative;
  top: -100px;
  animation: rotation 3s infinite linear;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
.check_ul {
  margin: 0;
  padding: 0;
  list-style: none;
  line-height: 19px;
  font-size: 14px;
}
.check_ul > * {
  display: block;
  font-family: "Roboto", sans-serif !important;
  position: relative;
  padding-left: 20px;
  margin-bottom: 8px;
}
.check_ul > *::before {
  left: 0;
  position: absolute;
  font-family: "Font Awesome 5 Free";
}
.check_ul > * > span {
  font-weight: normal;
  font-family: "Roboto", sans-serif;
}
.check_ul > .fa-circle:before {
  font-size: 8px;
  margin-top: 5px;
}

.blur {
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
}

.blur-blue {
  background: -webkit-linear-gradient(271.68deg, #21617D -10.74%, #4A7FCA -10.73%, #94B0ED 89.35%);
  box-shadow: 0 0px 10px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
}

.check_ul1 {
