.modal-view {
  position: fixed;
  z-index: 100000;
  top: -10vh;
  left: -10vw;
  width: 120vw;
  height: 120vh;
}
.modal-view .modal-drop {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  -webkit-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
}
.modal-view .modal-body {
  position: absolute;
  overflow: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 94vw;
  max-width: 500px;
  max-height: 96vh;
  padding: 15px;
  background: white;
  border-radius: 12px;
}
@media (min-width: 768px) {
  .modal-view .modal-body {
    padding: 30px 50px;
  }
}
