.show-more-inline::after {
  content: "show more";
  font-size: 13px;
  margin-left: 15px;
  color: #0C74FF;
  border-bottom: solid 1px #0C74FF;
  white-space: nowrap;
}

.post-content {
  position: relative;
  overflow: hidden;
}
.post-content.shorten {
  max-height: 200px;
}
.post-content.shorten.hidden::before {
  content: "show more";
  display: block;
  font-size: 13px;
  color: #0C74FF;
  width: 100%;
  text-align: center;
  bottom: 10px;
  position: absolute;
  z-index: 2;
}
.post-content.shorten.hidden::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100px;
  background: white;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, white 80%, white 100%);
}
.post-content * {
  margin-bottom: 0;
}
.post-content img {
  display: block;
  max-width: 100%;
  height: auto !important;
  margin: 15px auto;
}
.post-content iframe {
  max-width: 800px;
  display: block;
  margin: 15px auto;
  width: 100%;
  height: 250px;
}
@media (min-width: 768px) {
  .post-content iframe {
    height: 350px;
  }
}
@media (min-width: 992px) {
  .post-content iframe {
    height: 400px;
  }
}
.post-content h1 {
  /** h5 */
  font-size: 20px !important;
  font-weight: 700 !important;
  line-height: 26px !important;
  letter-spacing: 0.2px !important;
}
.post-content h2 {
  /** h6 */
  font-size: 18px !important;
  font-weight: 500 !important;
  line-height: 24px !important;
  letter-spacing: 0.2px !important;
}
.post-content h3, .post-content h4, .post-content h5, .post-content h6 {
  /** subtitle1 with fontWeightBold*/
  font-size: 16px !important;
  font-weight: 600 !important;
  line-height: 24px !important;
  letter-spacing: 0.25px !important;
}

.contenteditable {
